* {
  margin: 0;
  padding: 0;
}

.contactus {
  max-width: 1920px;
  margin: 0 auto;
  overflow: hidden;
}

.contactus-container {
  width: 1200px;
  margin: 0 auto;
  box-sizing: border-box;
}

.contactus-section {
  width: 100%;
  position: relative;
}

.contactus-banner {
  width: 100%;
  height: 640px;
  background-image: url("https://cdn.shopify.com/s/files/1/0560/1276/7399/files/contactus-banner.jpg?v=1625207595");
  background-size: cover;
  background-position: center center;
}

.contactus-banner-content {
  color: #fff;
  padding-top: 145px;
}

.contactus-banner-title {
  font-size: 56px;
  margin-bottom: 80px;
  position: relative;
  line-height: 1.38;
}

.contactus-banner-title::after {
  content: '';
  width: 50px;
  height: 8px;
  background: url("https://cdn.shopify.com/s/files/1/0560/1276/7399/files/aboutus-icon-deco-1_a063db63-4d07-407a-831b-385c19b412bc.png?v=1624951210");
  background-size: 100% 100%;
  position: absolute;
  left: 0;
  bottom: -20px;
}

.contactus-banner-desc {
  font-size: 24px;
  line-height: 1.68;
}

.contactus-ways-content {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 90px 0 1px 0;
}

.contactus-ways-item {
  width: 50%;
  text-align: center;
  padding: 0px 120px;
  color: #333333;
  box-sizing: border-box;
  margin-bottom: 80px;
}

.contactus-ways-href {
  text-decoration: none;
  display: inline-block;
  color: #333333;
}

.contactus-ways-icon {
  display: block;
  width: 120px;
  height: 120px;
  margin: 0 auto 20px;
  background-image: url("https://cdn.shopify.com/s/files/1/0560/1276/7399/files/contactus-icon-1.png?v=1625208946");
  background-size: 120px 480px;
}

.contactus-ways-icon-livechat {
  background-position: 0 0;
}

.contactus-ways-icon-email {
  background-position: 0 -120px;
}

.contactus-ways-icon-phone {
  background-position: 0 -240px;
}

.contactus-ways-icon-whole {
  background-position: 0 -360px;
}

.contactus-ways-title {
  font-size: 24px;
  margin-bottom: 28px;
}

.contactus-ways-desc {
  font-size: 14px;
  text-align: center;
  line-height: 1.68;
  margin-bottom: 30px;
  height: 80px;
}

.contactus-ways-info {
  font-size: 20px;
  text-align: center;
  color: #ff2d5f;
}

.contactus-essential {
  background-image: url("https://cdn.shopify.com/s/files/1/0560/1276/7399/files/contactus-pic-bg1.jpg?v=1625212604");
  background-size: 100% auto;
  background-repeat: repeat-y;
  padding: 70px 0 1px;
}

.contactus-essential-item {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-items: center;
  justify-content: center;
  box-shadow: 0px 5px 16px 0px rgba(216, 216, 216, 0.42);
  background: #fff;
  margin-bottom: 70px;
}

.contactus-essential-pic {
  width: 42%;
  display: block;
}

.contactus-essential-word {
  text-align: left;
  width: 58%;
  padding: 0 70px 0 70px;
}

.contactus-essential-title {
  font-size: 24px;
  color: #111;
  position: relative;
  line-height: 1.38;
  margin-bottom: 40px;
}

.contactus-essential-title::after {
  content: '';
  width: 32px;
  height: 2px;
  background: #e74b5c;
  position: absolute;
  left: 0;
  bottom: -20px;
}

.contactus-essential-desc {
  color: #333;
  font-size: 16px;
  line-height: 1.58;
}

@media (max-width: 1399px) {
  .contactus-container {
    width: 900px;
  }
}

@media (max-width: 1024px) {
  .contactus-container {
    width: 100%;
    padding-left: 40px;
    padding-right: 40px;
  }
  .contactus-ways-item {
    padding: 0 20px;
  }
  .contactus-ways-title {
    font-size: 20px;
  }
  .contactus-ways-desc {
    margin-bottom: 20px;
  }
  .contactus-ways-info {
    font-size: 17px;
  }
  .contactus-ways-content {
    padding: 60px 0 1px;
  }
  .contactus-ways-icon {
    width: 90px;
    height: 90px;
    margin: 0 auto 15px;
    background-size: 90px 360px;
  }
  .contactus-ways-icon-email {
    background-position: 0 -90px;
  }
  .contactus-ways-icon-phone {
    background-position: 0 -180px;
  }
  .contactus-ways-icon-whole {
    background-position: 0 -270px;
  }
  .contactus-essential {
    padding: 60px 0 1px;
  }
  .contactus-essential-item {
    margin-bottom: 50px;
  }
  .contactus-essential-word {
    padding: 0 20px;
  }
  .contactus-essential-title {
    font-size: 16px;
    margin-bottom: 20px;
  }
  .contactus-essential-title:after {
    bottom: -10px;
  }
  .contactus-essential-desc {
    font-size: 14px;
  }
}

@media (max-width: 767px) {
  .contactus-container {
    padding-left: 20px;
    padding-right: 20px;
  }
  .contactus-banner {
    height: 460px;
    background-size: 1380px 460px;
    background-position: 80%;
  }
  .contactus-banner-title {
    font-size: 28px;
  }
  .contactus-banner-title:after {
    height: 6px;
    bottom: -14px;
  }
  .contactus-banner-desc {
    font-size: 16px;
  }
  .contactus-banner-content {
    padding-top: 90px;
  }
  .contactus-ways-item {
    padding: 0 10px;
    margin-bottom: 40px;
  }
  .contactus-ways-title {
    font-size: 16px;
    margin-bottom: 10px;
  }
  .contactus-ways-desc {
    margin-bottom: 5px;
    font-size: 12px;
    line-height: 1.38;
    height: 100px;
  }
  .contactus-ways-info {
    font-size: 13px;
  }
  .contactus-ways-content {
    padding: 40px 0 1px;
  }
  .contactus-ways-icon {
    width: 60px;
    height: 60px;
    margin: 0 auto 15px;
    background-size: 60px 240px;
  }
  .contactus-ways-icon-email {
    background-position: 0 -60px;
  }
  .contactus-ways-icon-phone {
    background-position: 0 -120px;
  }
  .contactus-ways-icon-whole {
    background-position: 0 -180px;
  }
  .contactus-essential {
    padding: 40px 0 1px;
  }
  .contactus-essential-item {
    flex-wrap: wrap;
    margin-bottom: 35px;
  }
  .contactus-essential-pic {
    order: 1;
    width: 100%;
  }
  .contactus-essential-word {
    order: 2;
    width: 100%;
    padding: 20px;
  }
}
